import React, { useState, useEffect } from "react";
import {
  faArrowLeft,
} from "@fortawesome/free-solid-svg-icons";
import Swal from "sweetalert2";
import { useHistory, useParams } from "react-router-dom";
import jwt_decode from "jwt-decode";
import axios from "axios";
import {
  Card,
  Toast,
  Image,
  Button,
  ButtonGroup,
  Dropdown,
  Row,
  Container,
  Col,
} from "@themesberg/react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import moment from "moment-timezone";
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Out from "./invitationsOut";
import In from "./invitationsIn";
import Frnd from "./friends";

function TabPanel(props) {
    const { children, value, index, ...other } = props;
  
    return (
      <div
        role="tabpanel"
        hidden={value !== index}
        id={`simple-tabpanel-${index}`}
        aria-labelledby={`simple-tab-${index}`}
        {...other}
      >
        {value === index && (
          <Box sx={{ p: 3 }}>
            <Typography>{children}</Typography>
          </Box>
        )}
      </div>
    );
  }
  
  TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired,
  };
  
  function a11yProps(index) {
    return {
      id: `simple-tab-${index}`,
      'aria-controls': `simple-tabpanel-${index}`,
    };
  }
  
  export default function BasicTabs() {
    const [value, setValue] = React.useState(0);
    const [data, setData] = useState([]);
    const handleChange = (event, newValue) => {
      setValue(newValue);
    };
    const id = useParams().id;
    const fetchData = async () => {
        try {
          const { data: response } = await axios.get(
            "http://skiesbook.com:3000/api/v1/users/"+id
          );
          setData(response);
          console.log(response);
        } catch (error) {
          console.error(error.message);
        }
    };
    useEffect(() => {
        fetchData();
    }, []);
    
  
    return (
        <>
       
      <Box sx={{ width: '100%' }}>
        <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
          <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
            <Tab label="Parenté" {...a11yProps(0)} />
            <Tab label="Invitations envoyés" {...a11yProps(1)} />
            <Tab label="Invitations reçus" {...a11yProps(2)} />
          </Tabs>
        </Box>
        <TabPanel value={value} index={0}>
        {data?.profiles?.map((profile) => (
            <Frnd key={profile.id} id={profile} />
            ))}
        </TabPanel>
        <TabPanel value={value} index={1}>
         {data?.profiles?.map((profile) => (
            <Out key={profile.id} id={profile} />
            ))}
        </TabPanel>
        <TabPanel value={value} index={2}>
        {data?.profiles?.map((profile) => (
            <In key={profile.id} id={profile} />
            ))}
        </TabPanel>
      </Box>
      </>
    );
  }